import React from 'react';
import { Link } from "react-router-dom";
import { Button, ButtonGroup, Typography, AppBar, Toolbar, makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    buttonGroup: {
        marginLeft: 'auto',
    },
    titleStretch: {
        maxWidth: "100vw",
        backgroundColor: "var(--primary)",
    },
    toolBarContent: {
        margin: "0 25px 0px 10px",
        variant: "fullWidth",
        fontFamily: "Ubuntu",
        fontSize: "1.8vw",
        textAlign: "center",
        color: "black",
        paddingLeft: 0,
    },
}));

const Header = () => {
    const classes = useStyles();

    return (
        <div>
            <AppBar position='static' className={classes.titleStretch}>
                <Toolbar>
                    <Typography variant="h6" className={classes.toolBarContent}>Jonathan Meisner</Typography>
                    <ButtonGroup className={classes.buttonGroup}>
                            <Button component={Link} to="/" variant="outlined">Home</Button>

                            <Button disabled component={Link} to="/livecode" variant="outlined">Code Demos</Button>

                            <Button disabled component={Link} to="/contact" variant="outlined">Contact Me</Button>
                    </ButtonGroup>
                </Toolbar>
            </AppBar>
        </div>
    )
}

export default Header;